Bootstraps .button styles can be applied to other elements, such as `<label>`s, to provide checkbox or radio style button toggling. Add data-toggle="buttons" to a .btn-group containing those modified buttons to enable their toggling behavior via JavaScript and add .btn-group-toggle to style the `<input>`s within your buttons. Note that you can create single input-powered buttons or groups of them.

